@import url(https://fonts.googleapis.com/css2?family=Heebo:wght@300;400;500&family=Roboto:wght@400;500;700&display=swap);
header {
  display: flex;
  justify-content: space-between;
  margin: 40px 0;
}
header .header-logo,
.header-wellcome {
  display: flex;
  align-items: center;
}

header .header-logo span {
  font-size: 28px;
  font-weight: 300;
  line-height: 40px;
  color: var(--black-color);
  margin-left: 16.6px;
}

@media screen and (max-width: 414px) {
  header .header-wellcome p {
    display: none;
  }
}

header .header-wellcome p {
  font-size: 12px;
  line-height: 16px;
  color: var(--black-color);
  margin-right: 16px;
}

header .header-wellcome p span {
  font-weight: 700;
}

header .header-wellcome button {
  width: 32px;
  height: 32px;
  -webkit-clip-path: circle(50% at 50% 50%);
          clip-path: circle(50% at 50% 50%);
  cursor: pointer;
}

.not-found-body {
  width: 80%;
  margin: auto;
}

.not-found {
  display: flex;
  justify-content: center;
}

@media screen and (max-width: 414px) {
  .not-found-body .not-found img {
    width: 358px;
    align-items: center;
  }
}

form {
  display: flex;
  flex-flow: column nowrap;
}

form label {
  background-color: var(--background-label);
  -webkit-backdrop-filter: blur(2px);
          backdrop-filter: blur(2px);
  border-radius: 4px;
  padding: 8px 16px 8px 12px;
  display: flex;
  flex-flow: column nowrap;
}

form label input {
  font-size: 16px;
  line-height: 24px;
  box-shadow: 0;
  outline: 0;
  border: none;
  background-color: var(--background-label-input);
  color: white;
}

form span {
  font-size: 12px;
  line-height: 16px;
  color: white;
  opacity: 0.5;
  margin-bottom: 4px;
}

form .password-label {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  margin-top: 16px;
}

form .password-label .pass {
  display: flex;
  flex-flow: column nowrap;
}

form .password-label button {
  background-color: white;
  border-radius: 44px;
  color: var(--author-color-name);
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  width: 85px;
  margin: 4px 0;
  cursor: pointer;
}

form .error-message {
  width: 239px;
  margin-top: 16px;
}

.login-background {
  background-image: url(/static/media/background-image.0e957769.svg);
  background-repeat: no-repeat, repeat;
  background-position: center;
  background-size: cover;

  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  padding-left: 115px;
}

@media screen and (max-width: 320px) {
  .login-background {
    padding: 0;
    background-position-x: -272px;
  }
}

@media screen and (min-width: 375px) {
  .login-background {
    background-position-x: -355px;
  }
}

@media screen and (min-width: 414px) {
  .login-background {
    background-position-x: -410px;
  }
}

@media screen and (max-width: 414px) {
  .login-background {
    padding: 0;
  }

  .login-background .form-body {
    width: 90%;
    margin: auto;
    height: 224px;
    display: flex;
    flex-flow: column nowrap;
  }

  .login-background .form-body .form label {
    width: 100%;
    display: flex;
  }

  .login-background .form-body .form .password-label input {
    width: 157px;
  }
}

@media screen and (min-width: 415px) {
  .login-background {
    background-position-x: center;
    background-position-y: inherit;
  }
}

.login-background .form-body {
  width: 85%;
  height: 224px;
  display: flex;
  flex-flow: column nowrap;
}

.login-background .form-body label {
  width: 368px;
  display: flex;
}

.login-background .form-body .form-header {
  display: flex;
  align-items: center;
  margin-bottom: 48px;
}

.login-background .form-body .form-header span {
  font-size: 28px;
  font-weight: 300;
  line-height: 40px;
  color: white;
  margin-left: 16.6px;
}

.login-background .form-body .form .password-label button {
  width: 85px;
  font-weight: 700;
  font-size: 16px;
}

.main-content {
  list-style: none;
  display: flex;
  flex-flow: row wrap;
  width: 100%;
  margin: auto;
}

.main-content button {
  background-color: var(--background-color);
  flex: 1 0 25%;
  margin: 0 14px 16px 0;
}
.main-content .book-card {
  background-color: white;
  box-shadow: 0px 6px 24px #54105f21;
  border-radius: 4px;
  cursor: pointer;
  display: flex;
  justify-content: start;
  align-items: center;
  padding: 0 16px;
}

.main-content .book-card .book-information {
  margin-left: 16px;
  display: flex;
  justify-content: space-around;
  flex-flow: column;
  line-height: 14px;
  text-align: start;
}

.main-content .book-card .book-img {
  width: 81px;
}

.main-content .book-card .book-information .title-and-author {
  margin-bottom: 10px;
}

.main-content .book-card .book-information .book-description p {
  padding-bottom: 3px;
}

@media screen and (max-width: 414px) {
  .main-content button {
    margin: 0 0 16px 0;
  }
}

@media screen and (max-width: 1669px) {
  .main-content button {
    width: 272px;
    margin: 0 0 16px 0;
  }
  .main-content .book-card {
    height: 160px;
    /* width: 300px; */
    margin-right: 16px;
  }
  .main-content .book-card img {
    width: 81px;
    height: 122px;
  }

  .main-content .book-card .book-information {
    height: 125px;
  }
}

@media screen and (min-width: 1670px) {
  .main-content h1 {
    font-size: 16px;
  }
  .main-content h2,
  .main-content p {
    font-size: 14px;
  }

  .main-content button {
    flex: 1 0 20%;
  }

  .main-content .book-card {
    height: 190px;
  }

  .main-content .book-card .book-img {
    width: 111px;
  }

  .main-content .book-card img {
    width: 111px;
    height: 151px;
  }

  .main-content .book-card .book-information {
    margin-left: 16px;
    height: 153px;
    justify-content: space-around;
  }
  .main-content .book-card .book-information .title-and-author {
    margin-bottom: 10px;
  }
}

.book-details {
  display: flex;
  flex-flow: column;
  background: var(--background-label);
  -webkit-backdrop-filter: blur(2px);
          backdrop-filter: blur(2px);
  width: 100vw;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
}

.book-details .details-button {
  display: flex;
  justify-content: end;
  margin: 16px 17px 32px auto;
}

.book-details .details-button button {
  width: 32px;
  height: 32px;
  cursor: pointer;
  -webkit-clip-path: circle(50% at 50% 50%);
          clip-path: circle(50% at 50% 50%);
}

.book-details .main-details {
  display: flex;
  align-self: center;
  width: 769px;
  padding: 48px;
  background-color: white;
  box-shadow: 0px 16px 80px #00000052;
  border-radius: 4px;
}

.book-details .main-details .details-book-img img {
  width: 349px;
  height: 512.29px;
  margin-right: 48px;
}

.book-details .main-details .details .book-title {
  margin-bottom: 32px;
}

.book-details .main-details .details .book-title h1 {
  font-size: 28px;
  line-height: 40px;
}

.book-details .main-details .details .informaction p {
  color: var(--black-color);
  font-weight: 500;
  font-size: 12px;
}

.book-details .main-details .details .informaction .content-information div {
  display: flex;
  justify-content: space-between;
}

.book-details
  .main-details
  .details
  .informaction
  .content-information
  div
  span {
  font-size: 12px;
  color: var(--book-discription-color);
}

.book-details .main-details .details .review {
  margin-top: 32px;
}

.book-details .main-details .details .review {
  margin-top: 32px;
}

.book-details .main-details .details .review p:nth-child(1) {
  color: var(--black-color);
  font-weight: 500;
  font-size: 12px;
  margin-bottom: 10px;
}

.book-details .main-details .details .review p:nth-child(2) img {
  margin-right: 5px;
}

@media screen and (min-width: 415px) {
  .main-content {
    display: flex;
  }
}

@media screen and (max-width: 414px) {
  .book-details {
    height: 200vh;
    width: 100%;
  }

  .book-details .details-button {
    margin: 16px 17px 16px auto;
  }

  .book-details .main-details {
    flex-direction: column;
    width: 90%;
    margin: 0 auto;
    padding: 20px;
  }

  .book-details .main-details .details-book-img img {
    width: 240px;
    height: 351px;
    margin: 0;
    -webkit-filter: drop-shadow(0px 11.9008px 17.8512px rgba(0, 0, 0, 0.3));
            filter: drop-shadow(0px 11.9008px 17.8512px rgba(0, 0, 0, 0.3));
  }
}

@media screen and (max-width: 414px) {
  main .change-page {
    align-self: center;
    padding-bottom: 16px;
  }

  main .change-page p {
    order: 0;
    margin-right: 16px;
  }

  main .change-page .previous {
    order: -1;
    margin-right: 16px;
  }
}

main {
  display: flex;
  flex-flow: column nowrap;
}

main .change-page {
  display: flex;
  align-items: center;
  justify-content: end;
}

main .change-page p {
  font-size: 12px;
  color: var(--black-color);
  margin-right: 16px;
}

main .change-page p span {
  font-weight: 700;
}

main .change-page button {
  width: 32px;
  height: 32px;
  cursor: pointer;
  -webkit-clip-path: circle(50% at 50% 50%);
          clip-path: circle(50% at 50% 50%);
}

main .change-page div button:nth-child(1) {
  margin-right: 8px;
}

.home {
  width: 85%;
  margin: auto;
}

:root {
  --black-color: #333333;
  --author-color-name: #ab2680;
  --book-discription-color: #999999;
  --background-color: #e9e0e6;
  --background-label: #00000052;
  --background-label-input: #00000000;
}

* {
  box-sizing: border-box;
  border: none;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Heebo", sans-serif;
  background-color: #e9e0e6;
  background-color: var(--background-color);
}

h1 {
  color: #333333;
  color: var(--black-color);
  font-size: 14px;
  font-weight: 500;
}

h2 {
  color: #ab2680;
  color: var(--author-color-name);
  font-size: 12px;
}

p {
  color: #999999;
  color: var(--book-discription-color);
  font-size: 12px;
}

