.login-background {
  background-image: url("../../images/background-image.svg");
  background-repeat: no-repeat, repeat;
  background-position: center;
  background-size: cover;

  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  padding-left: 115px;
}

@media screen and (max-width: 320px) {
  .login-background {
    padding: 0;
    background-position-x: -272px;
  }
}

@media screen and (min-width: 375px) {
  .login-background {
    background-position-x: -355px;
  }
}

@media screen and (min-width: 414px) {
  .login-background {
    background-position-x: -410px;
  }
}

@media screen and (max-width: 414px) {
  .login-background {
    padding: 0;
  }

  .login-background .form-body {
    width: 90%;
    margin: auto;
    height: 224px;
    display: flex;
    flex-flow: column nowrap;
  }

  .login-background .form-body .form label {
    width: 100%;
    display: flex;
  }

  .login-background .form-body .form .password-label input {
    width: 157px;
  }
}

@media screen and (min-width: 415px) {
  .login-background {
    background-position-x: center;
    background-position-y: inherit;
  }
}

.login-background .form-body {
  width: 85%;
  height: 224px;
  display: flex;
  flex-flow: column nowrap;
}

.login-background .form-body label {
  width: 368px;
  display: flex;
}

.login-background .form-body .form-header {
  display: flex;
  align-items: center;
  margin-bottom: 48px;
}

.login-background .form-body .form-header span {
  font-size: 28px;
  font-weight: 300;
  line-height: 40px;
  color: white;
  margin-left: 16.6px;
}

.login-background .form-body .form .password-label button {
  width: 85px;
  font-weight: 700;
  font-size: 16px;
}
